.s-items-img-btn {
  &__title {
    text-align: center;
    margin-bottom: rem(53); } }


.items-img-btn {
  padding: rem(61) rem(85);
  background-color: $accent-thrd;

  @include lg {
    padding: rem(40) rem(50); }

  @include md {
    padding: rem(30) rem(15); }

  &__item {
    @include notlast {
      margin-bottom: rem(40);
      @include md {
        margin-bottom: rem(20); } } } }

.item-img-btn {
  background-color: $white;
  padding-left: rem(21);

  display: flex;
  align-items: center;

  @include not-rtl {
    padding-left: 0;
    padding-right: rem(21); }

  @include sm {
    flex-wrap: wrap;

    padding-left: 0 !important;
    padding-right: 0 !important;
    padding-bottom: rem(15); }

  @include xs {
    text-align: center; }

  &__img {
    font-size: 0;
    &--gap {
      padding: rem(15) rem(30); }

    @include sm {
      width: 30%; }

    @include xs {
      width: 100%;
      margin-bottom: rem(15); } }

  &__text {
    font-size: em(18);
    padding-left: rem(14);
    padding-right: rem(14);
    flex-grow: 1;

    @include sm {
      width: 70%; }

    @include xs {
      width: 100%; } }

  &__btn {
    @include sm {
      width: 100%;
      margin-top: rem(15);
      text-align: center;

      padding-left: rem(15);
      padding-right: rem(15); } } }
