.s-list {
  &__img {
    position: relative;
    padding-left: rem(15);
    padding-bottom: rem(20);
    margin-bottom: rem(52);

    @include before {
      position: absolute;
      z-index: 0;
      bottom: 0;
      left: 0;
      top: 10%;
      right: 10%;
      background-color: $accent-sec; }

    img {
      position: relative;
      z-index: 1;
      width: 100%; } }

  &__subttl {
    margin-bottom: rem(40); }

  &__inner {
    padding: 0 rem(65) rem(50);
    @include lg {
      padding-left: rem(15);
      padding-right: rem(15);
      padding-bottom: rem(30); } } }
