.slider {
  &__wrap {
    direction: ltr;
    position: relative;
    overflow: hidden; }

  &--home {
    &__wrap {
      [data-action="stop"], [data-action="start"] {
        display: none; }
      .tns-ovh {
        padding-top: rem(60);
        padding-bottom: rem(70); }
      .tns-nav {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        text-align: center;
        button {
          display: inline-block;

          width: rem(20);
          height: rem(20);

          margin-left: rem(7);
          margin-right: rem(7);

          border-radius: 50%;
          background-color: #e2e2e2;

          &.tns-nav-active {
            background-color: $accent; } } } } } }

.home-slide {
  padding-left: rem(110);
  padding-right: rem(110);
  padding-top: rem(22);
  padding-bottom: rem(35);

  background-color: $accent;
  color: $white;

  display: inline-flex !important;
  flex-wrap: wrap;

  direction: rtl;
  font-size: 1rem !important;

  @include not-rtl {
    direction: ltr; }

  @include lg {
    padding-left: rem(50);
    padding-right: rem(50); }

  @include md {
    padding-left: rem(15);
    padding-right: rem(15); }

  &__title {
    margin-bottom: rem(50);
    margin-top: rem(-70);
    text-align: left;

    @include sm {
      margin-top: auto;
      order: 0;
      margin-bottom: rem(15);
      text-align: center; } }

  &__btn {
    text-align: left;
    @include sm {
      order: 1;
      text-align: center; } }

  &__img {
    width: percentage(335 / 1203);
    font-size: 0;
    margin-bottom: rem(-35) + rem(-70);  // padding bottom + offset

    @include vertical;

    .lazy--loaded {
      display: inline-block;
      vertical-align: bottom; }

    @include sm {
      width: 100%;
      margin-bottom: rem(15);
      order: 1; } }

  &__content {
    width: percentage((1203 - 335) / 1203);
    @include sm {
      width: 100%;

      display: flex;
      flex-direction: column; } }


  &__quote {
    max-width: 100%;
    margin-right: auto;
    margin-left: rem(-19);
    margin-top: rem(-60) + rem(-22); // padding top + offset

    @include sm {
      margin-top: 0;
      order: 2;
      margin-left: auto;
      margin-right: auto; } } }

