html {
  font-size: $base-fz + px;
  line-height: 1.375;
  font-weight: 700;
  color: $c-text;
  @include reg;
  @include sm {
    font-size: $base-fz-sm + px; } }

body {
  background-color: $white;
  min-width: 320px;
  @include no-scroll; }

.icon {
  display: inline-block;
  fill: currentColor;
  font-size: inherit; }

.lazy {
  $this: &;
  &--loaded:not(#{$this}--no-fade-in) {
    animation: fadeIn 1s; } }

.out {
  @include md {
    padding-top: $header-height-md; } }

.quote {
  color: $c-text;
  font-size: rem(336);
  width: em(754, 336);
  height: 1em;

  padding: em(55, 336) em(55, 336) em(110, 336);

  @include image('Union.svg');
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: contain;

  @include md {
    font-size: rem(280); }

  @include xs {
    font-size: rem(200); }
  @include xxs {
    font-size: rem(170); }

  &__inner {
    font-size: em(30, 336);
    max-height: 5.1em;
    overflow: hidden; } }


.section-block {
  @include notlast {
    margin-bottom: rem(15); }

  &__title {
    margin-bottom: rem(16); }

  &__text {
    font-weight: 400; } }

.section-btn {
  color: $white;
  text-align: center; }

.title-label {
  display: inline-block;

  min-width: rem(408);
  padding: rem(20) rem(73) rem(15);
  margin-bottom: rem(30);

  background-color: $accent;
  color: $white;

  @include sm {
    min-width: rem(300); }

  &--accent-sec {
    background-color: $accent-sec; }

  &--accent-thrd {
    background-color: $accent-thrd;
    color: $c-text; } }


.text-photos {
  padding-left: rem(25);
  padding-right: rem(25);

  @include xs {
    padding-left: rem(15);
    padding-right: rem(15); }

  &__block {
    margin-bottom: rem(75);
    &:nth-child(1) {
      padding-right: rem(70);
      @include lg {
        padding-right: rem(15); } }
    &:nth-child(2) {
      padding-left: rem(80);
      @include lg {
        padding-left: rem(15); } }

    @include xs {
      padding-left: 0 !important;
      padding-right: 0 !important; } } }

.text-photos-block {
  $this: &;

  display: flex;
  flex-wrap: wrap;

  margin-left: rem(-25);
  margin-right: rem(-25);

  @include xs {
    margin-left: 0;
    margin-right: 0; }

  &__text {
    width: percentage(871 / 1352);
    padding-left: rem(25);
    padding-right: rem(25);
    @include lg {
      padding-left: 0;
      padding-right: 0; }

    @include xs {
      width: 100%;
      order: 1; } }

  &__img {
    width: percentage((1352 - 871) / 1352);
    padding-left: rem(25);
    padding-right: rem(25);

    @include xs {
      width: 100%;
      margin-bottom: rem(15);
      padding-left: 0;
      padding-right: 0;
      margin-right: rem(-15);

      @include not-rtl {
        margin-right: 0;
        margin-left: rem(-15); } } }

  &--right {
    margin-right: rem(-50);

    @include not-rtl {
      margin-right: 0;
      margin-left: rem(-50); }

    @include xs {
      margin-left: 0 !important;
      margin-right: 0 !important; } }


  &--sm {
    #{$this}__img {
      width: 50%;
      @include xs {
        width: 100%;

        margin-left: 0 !important;
        margin-right: 0 !important;

        text-align: center; } }
    #{$this}__text {
      width: 50%;
      @include xs {
        width: 100%; } } } }


.text-img {
  position: relative;
  display: inline-block;
  font-size: 0;
  @include before {
    position: absolute;
    z-index: 0; }

  img {
    position: relative;
    z-index: 1;
    width: 100%; }

  &--sm {
    padding-left: rem(45);
    padding-right: rem(45);
    @include before {
      top: rem(5);
      bottom: rem(5);
      left: 0;
      right: 0; } }

  &--md {
    padding-right: rem(24);
    padding-top: rem(21);
    padding-bottom: rem(21);
    @include before {
      top: 0;
      bottom: 0;
      right: 0;
      left: rem(100); } }

  &--md-side {
    padding: 0 rem(25);
    @include before {
      left: 0;
      right: 0;
      top: rem(25);
      bottom: rem(25); } }

  &--accent-sec {
    @include before {
      background-color: $accent-sec; } }

  &--accent {
    @include before {
      background-color: $accent; } } }


.right-images-block {
  @include clr;

  &__subttl {
    margin-bottom: rem(20);
    padding-left: rem(15);
    padding-right: rem(15); }

  &__imgs {
    float: left;

    width: 30%;
    padding-top: 60%;

    margin-right: rem(40);
    margin-bottom: rem(40);

    @include not-rtl {
      float: right;
      margin-right: 0;
      margin-left: rem(40); }

    @include r(1660) {
      padding-top: 70%;
      width: 37%; }

    @include xs {
      float: none;
      margin-left: auto;
      margin-right: auto; } }

  &__block {
    margin-bottom: rem(39); }

  &__content {
    padding-right: rem(48);
    padding-left: rem(20);

    @include not-rtl {
      padding-right: rem(20);
      padding-left: rem(48); }

    @include lg {
      padding-left: rem(15) !important;
      padding-right: rem(15) !important; } } }

.right-images {
  position: relative;

  &__img {
    position: absolute;
    display: inline-block;
    z-index: 1;
    @include before {
      position: absolute;
      z-index: 0; }
    img {
      position: relative;
      z-index: 1;
      width: 100%; }

    &:nth-child(1) {
      top: 0;
      left: 0;
      z-index: 0;

      padding: 3%;
      padding-right: 0;
      @include before {
        background-color: $accent-thrd;
        top: 0;
        bottom: 0;
        left: 0;
        right: 3%; } }

    &:nth-child(2) {
      top: 35%;
      right: 0;
      z-index: 1;
      max-width: 60%;

      padding: 4%;
      padding-bottom: 0;
      @include before {
        background-color: $accent-sec;
        top: 0;
        left: 0;
        right: 0;
        bottom: 40%; } }

    &:nth-child(3) {
      bottom: 0;
      left: 0;
      z-index: 0;
      max-width: 50%;

      padding: 5%;
      padding-left: 0;
      @include before {
        background-color: $accent;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0; } }

    &:nth-child(4) {
      bottom: 3%;
      right: 10%;
      max-width: 40%;

      padding: 0 3% 2% 0;
      @include before {
        background-color: $accent-thrd;
        bottom: 0;
        right: 0;
        top: 8%;
        left: 10%; } } } }
