.s-contacts {

  padding-top: rem(125);
  padding-bottom: rem(130);
  overflow: hidden;

  @include lg {
    padding-top: rem(100);
    padding-bottom: rem(100); }

  @include md {
    padding-top: rem(50);
    padding-bottom: rem(50); }

  &__inner {
    display: flex;
    flex-wrap: wrap; }

  &__item {
    width: 33.33333%;
    margin-bottom: rem(25);
    @include xs {
      width: 100%; } } }
