.footer {
  background-color: $dark;
  color: $white;

  &__inner {
    padding: rem(70) rem(122) rem(58) rem(40);
    display: flex;

    @include md {
      padding-left: rem(15);
      padding-right: rem(15); } }

  &__meta {
    flex-grow: 1;
    padding-left: 1rem;
    @include not-rtl {
      padding-left: 0;
      padding-right: 1rem; } }

  &__meta-item {
      @include notlast {
        margin-bottom: rem(20); } } }

