.s-label-aupair {
  padding-top: rem(44);
  padding-bottom: rem(30);

  &__title {
    padding-left: rem(15);
    padding-right: rem(15); }

  &__prices {
    margin-bottom: rem(55); }

  &__title-sm {
    margin-bottom: rem(25); } }

.s-label-text-images {
  padding-top: rem(40);
  padding-bottom: rem(75);

  @include lg {
    padding-bottom: rem(40); }

  &__block-sm {
    max-width: 1090px;
    margin: 0 auto;
    align-items: center;
    margin-bottom: rem(45);
    padding-left: rem(15);

    @include not-rtl {
      padding-left: 0;
      padding-right: rem(15); }

    @include xs {
      padding-left: rem(15) !important;
      padding-right: rem(15) !important; } } }

.s-label-dates {
  padding-top: rem(75);
  padding-bottom: rem(30);

  @include lg {
    padding-top: rem(30); } }

.s-label-blocks {
  padding-top: rem(30);
  padding-bottom: rem(35); }

.s-label-list {
  padding-top: rem(35);
  padding-bottom: rem(25);

  &__block {
    display: flex;
    align-items: center;
    flex-wrap: wrap;

    margin-bottom: rem(54);

    @include md {
      align-items: flex-start; } }

  &__img-wrap {
    width: rem(316);
    max-width: 30%;
    @include xs {
      max-width: none;
      width: 100%;
      margin-bottom: rem(15); } }

  &__content {
    max-width: 70%;
    width: 100%;
    padding: 0 rem(63);

    @include lg {
      padding: 0 rem(15); }

    @include xs {
      max-width: none; } }

  &__img {
    position: relative;
    padding: 7% 25%;
    padding-left: 0;

    @include before {
      background-color: $accent;
      position: absolute;
      z-index: 0;
      top: 0;
      right: 0;
      bottom: 0;
      left: rem(80); }

    img {
      position: relative;
      z-index: 1;
      width: 100%; } }

  &__list-blocks {
    display: flex;
    flex-wrap: wrap;

    max-width: 1110px;
    padding-left: rem(15);
    padding-right: rem(15);

    margin: 0 auto;
    margin-bottom: rem(109);

    @include lg {
      margin-bottom: rem(80); }

    @include md {
      margin-bottom: rem(30); } }


  &__list-block {
    width: 50%;
    padding-left: rem(15);

    @include xs {
      width: 100%; } }

  &__block-title {
    margin-bottom: rem(40); } }

.s-label-text {
  padding-top: rem(30);
  padding-bottom: rem(45);

  &__text {
    max-width: 1110px;
    margin: 0 auto;
    padding: 0 rem(15); } }


.s-label-img-blocks {
  padding-top: rem(45);
  padding-bottom: rem(107);

  @include lg {
    padding-bottom: rem(80); }
  @include md {
    padding-bottom: rem(45); }

  &__block {
    display: flex;
    flex-wrap: wrap;

    margin-bottom: rem(78);

    @include lg {
      margin-bottom: rem(50); }
    @include md {
      margin-bottom: rem(30); } }

  &__img-wrap {
    width: rem(550);
    max-width: 40%;
    @include xs {
      max-width: none;
      width: 100%; } }

  &__text {
    width: 100%;
    max-width: 60%;

    padding-left: rem(64);
    padding-right: rem(65);
    padding-top: rem(80);

    @include lg {
      padding: rem(15); }
    @include xs {
      max-width: none; } }

  &__text-in {
    max-width: rem(616); }

  &__img {
    position: relative;
    font-size: 0;

    padding: 0 0 5% 3%;
    @include before {
      position: absolute;
      z-index: 0;
      background-color: $accent-thrd;

      top: 25%;
      bottom: 0;
      left: 0;
      right: 0; }

    img {
      position: relative;
      z-index: 1;
      width: 100%; } } }
