.blocks {
  display: flex;
  flex-wrap: wrap;

  margin-left: rem(-12);
  margin-right: rem(-12);

  &__wrap {
    padding-left: rem(24);
    padding-right: rem(24); }

  &__item {
    width: 33.33333%;
    padding-left: rem(12);
    padding-right: rem(12);
    margin-bottom: rem(24);

    @include md {
      width: 50%; }
    @include xs {
      width: 100%; } } }

.block {
  $this: &;

  height: 100%;

  &__btn {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

    width: 100%;
    height: 100%;

    color: $white;
    text-align: center;
    background-color: $accent;
    position: relative;

    padding: rem(32) rem(50) rem(14);

    @include tr(background-color .3s);
    @include hover {
      background-color: $accent-dark; }

    @include after {
      @include arr(2.5em, 1em, currentColor, b); }

    @include lg {
      padding: rem(20) rem(20) rem(14); }

    @include md {
      padding: rem(14); }

    &--active {
      height: auto;
      margin-bottom: rem(3); } }

  &__title {
    margin-bottom: rem(15);
    flex-grow: 1;
    display: flex;
    align-items: center; }

  &__list-wrap {
    background-color: $accent;

    max-height: 0;
    overflow: hidden;

    @include tr(max-height .4s); }

  &__list-title {
    margin-bottom: rem(30);
    padding: rem(34) rem(22) 0 rem(62);
    @include xs {
      padding: rem(30) rem(15) 0; } }

  &__list {
    font-size: em(14);
    font-weight: 400;

    padding: 0 rem(22) rem(52) rem(62);

    @include xs {
      padding: 0 rem(15) rem(30); } }

  &__list-item {
    @include notlast {
      margin-bottom: rem(5); } }

  &__list-icon {
    fill: $white;
    margin-left: .5em;

    @include not-rtl {
      margin-left: 0;
      margin-right: .5em; } }


  &--accent-sec {
    #{$this}__btn, #{$this}__list-wrap {
      background-color: $accent-sec; }

    #{$this}__btn {
      @include hover {
        background-color: $accent-sec-dark; } } }

  &--accent-thrd {
    #{$this}__btn, #{$this}__list-wrap {
      background-color: $accent-thrd; }

    #{$this}__btn {
      color: $dark-aux;
      @include hover {
        background-color: $accent-thrd-dark; } } } }

