.header {
  position: relative;
  z-index: 10;

  @include md {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    box-shadow: 0 0 10px rgba($black, .15); }

  &__top {
    background-color: $accent;
    position: relative;
    z-index: 1;
    color: $white;
    overflow: hidden;

    @include before {
      position: absolute;
      z-index: 0;
      top: calc(100% - #{rem(40)});
      left: 50%;
      transform: translate(-50%, 0);

      width: rem(250);
      height: rem(250);

      background-color: $white;
      border-radius: 50%;

      @include md {
        width: rem(150);
        height: rem(80);
        top: calc(100% - #{rem(25)}); }
      @include sm {
        display: none; } } }

  &__top-in {
    display: flex;
    align-items: center;

    padding: rem(10) rem(35);

    height: $header-top-height;

    @include lg {
      padding: rem(10) rem(15); }
    @include md {
      height: $header-top-height-md; } }

  &__bottom {
    background-color: $white;
    position: relative;
    z-index: 2; }


  &__bottom-in {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    position: relative;
    z-index: 1;
    padding: rem(10) rem(35) 0;
    height: $header-bottom-height;

    @include md {
      height: $header-bottom-height-md;
      align-items: center;
      flex-direction: row;

      padding-left: rem(15);
      padding-right: rem(15);
      padding-top: 0; } }

  &__logo {
    display: block;
    position: relative;
    z-index: 10;

    margin-left: auto;
    margin-right: auto;
    width: rem(163);

    margin-top: rem(-30);
    margin-bottom: rem(10);

    img {
      position: relative; }

    @include md {
      width: rem(100);
      margin-bottom: 0; }

    @include sm {
      margin-top: 0;
      width: rem(70); } }

  &__contacts {
    display: flex;
    align-items: center;
    justify-content: flex-end;

    flex-grow: 1;
    font-size: em(18);

    @include xs {
      font-size: em(16); }
    @include xxs {
      font-size: em(14); } }

  &__contact {
    @include notlast {
      margin-left: rem(60);

      @include not-rtl {
        margin-left: 0;
        margin-right: rem(60); }

      @include md {
        margin-left: rem(10);
        @include not-rtl {
          margin-left: 0;
          margin-right: rem(10); } } }
    a {
      display: inline-flex;
      align-items: center;
      .icon {
        font-size: 1.4em;
        margin-right: .5em;
        @include not-rtl {
          margin-right: 0;
          margin-left: .5em; } } } }

  &__nav {
    @include md {
      position: absolute;
      top: 100%;
      left: 0;
      right: 0;
      background-color: $white;

      max-height: 0;
      overflow: hidden;

      @include ready {
        @include tr(max-height .5s); } } }

  &__burger {
    position: absolute !important;
    z-index: 10;
    top: 50%;
    transform: translate(0, -50%);
    left: rem(15);

    @include not-rtl {
      left: auto;
      right: rem(15); }

    @include mdmin {
      display: none; } } }

.burger {
  display: block;
  width: rem(28);
  height: rem(28);
  position: relative;
  span {
    display: block;
    &, &::after, &::before {
      width: 100%;
      height: 2px;
      background-color: currentColor;
      backface-visibility: hidden;
      border-radius: 2px;
      @include tr(transform .3s); }
    &::after, &::before {
      content: "";
      position: absolute;
      left: 0; }
    &::after {
      top: 5px; }
    &::before {
      bottom: 5px; } }
